import cloneDeep from 'lodash/cloneDeep'
import get from 'lodash/get'
import pick from 'lodash/pick'
import { useCallback, useContext, useMemo, useState } from 'react'

import { SearchContext } from '../../providers'
import { createPromptMessage, hasErrors, hydrateTree, makeIndex } from './util'
import { getBindingSlots } from '../../keymap'

export default function useBindingEditor (binding, paramIndex, onUpdate) {
  const context = useContext(SearchContext)
  const { sources } = context
  const [editing, setEditing] = useState(null)

  const bindTree = useMemo(() => (
    hydrateTree(binding, context)
  ), [binding, context])

  const paramSlice = paramIndex === undefined ? [0] : [paramIndex, paramIndex + 1]

  const bind = binding.value
  const behavior = get(sources.behaviours, bind, {})
  const behaviorParams = getBindingSlots(binding, context.behaviorParameters).slice(...paramSlice)
  const bindTreeParams = bindTree.params.slice(...paramSlice)
  const index = makeIndex(bindTree)
  const invalid = useMemo(() => hasErrors(index), [index])

  const selectNodeForEditing = useCallback(({ node, type = 'value' }) => {
    const prompt = createPromptMessage(node.slot, node, type)
    setEditing({ node, type, nodeIndex: index.indexOf(node), prompt })
  }, [index, setEditing])

  const clearEditing = useCallback(() => setEditing(null), [setEditing])

  const handleUpdateTree = useCallback(({ value, params }) => {
    const { nodeIndex } = editing
    const updated = cloneDeep(bindTree)
    const index = makeIndex(updated)
    const targetNode = index[nodeIndex]

    // Assume these are all fine?
    targetNode.value = value
    targetNode.params = params
    delete targetNode._isPlaceholder

    index.forEach(node => {
      delete node.source
      delete node.slot
    })

    onUpdate(pick(updated, ['value', 'params']))
    clearEditing()
  }, [bindTree, editing, onUpdate, clearEditing])

  const handleUpdateParams = useCallback(params => {
    const { nodeIndex } = editing
    const updated = cloneDeep(bindTree)
    const index = makeIndex(updated)
    const targetNode = index[nodeIndex]

    // Assume these are all fine?
    targetNode.params = params

    index.forEach(node => {
      delete node.source
      delete node.slot
    })

    onUpdate(pick(updated, ['value', 'params']))
    clearEditing()
  }, [bindTree, editing, onUpdate, clearEditing])

  const handleUpdateNode = useCallback((node, nodeIndex, source) => {
    const updated = cloneDeep(bindTree)
    const index = makeIndex(updated)
    const targetNode = index[nodeIndex]

    // Attempt to cast raw inputs to non-string type if such a type is defined
    // and I've specifically accounted for casting to that type here.
    // No error handling yet, so...
    if (node.slot.raw) {
      if (node.slot.type === 'number') {
        source.code = Number(source.code)
      }
    } else if (node.slot.enum) {
      const oldAdditionalParams = targetNode.source?.additionalParams?.length || 0
      const newAdditionalParams = source.additionalParams?.length || 0
      // When switching from a command parameter that has additionalParams to
      // another command parameter that has fewer (or none), the selected values
      // are still present in the tree but aren't rendered. Therefore it's still
      // possible for the unused values to be saved when the keymap is written.
      // This technically doesn't cause errors because behaviours are written to
      // ignore these values, but it can be confusing.
      // TODO: look into actually making sure that we aren't sending these
      // phantom values when submitting keymap state.
      if (newAdditionalParams < oldAdditionalParams) {
        // technically this could be anywhere in the tree but lets assume for
        // convenience that switching a "command" param can only happen to the
        // first child of the behavior bind.
        const tree = index[0]
        tree.params.splice(1, tree.params.length - 1)
        index.splice(2, index.length - 1)
      }
    }

    targetNode.value = source.code
    delete targetNode._isPlaceholder

    const newParams = targetNode.slot === 'behaviour'
      ? (context.behaviorParameters[targetNode.value] || [])
      : (source?.params || [])

    // Clear parameters if the new source params list doesn't match the old one.
    // TODO: instead of a reduce and iterating through the entire array in all
    // cases, this could look for the first index where both params lists don't
    // match and then splice targetNode.params from there
    targetNode.params = newParams.reduce((params, p, i) => {
      if (targetNode.source?.params?.[i] === newParams[i]) {
        params.push(targetNode.params[i])
      } else {
        params.push({ value: 0, _isPlaceholder: true })
      }
      return params
    }, [])

    index.forEach(node => {
      delete node.source
      delete node.slot
    })

    onUpdate(pick(updated, ['value', 'params', '_isPlaceholder']))
    clearEditing()
  }, [bindTree, context.behaviorParameters, onUpdate, clearEditing])

  const handleUpdateBindValue = useCallback(source => {
    if (editing.type === 'tree') {
      return handleUpdateTree(source)
    }

    if (editing.type === 'params') {
      return handleUpdateParams(source) // ugh
    }

    const { node, nodeIndex } = editing
    handleUpdateNode(node, nodeIndex, source)
  }, [editing, handleUpdateNode, handleUpdateTree, handleUpdateParams])

  const state = useMemo(
    () => ({
      behavior,
      behaviorParams,
      bindTree,
      bindTreeParams,
      editing,
      index,
      invalid
    }),
    [behavior, behaviorParams, bindTree, bindTreeParams, editing, index, invalid]
  )

  const callbacks = useMemo(
    () => ({
      clearEditing,
      handleUpdateBindValue,
      handleUpdateNode,
      selectNodeForEditing
    }),
    [
      selectNodeForEditing,
      clearEditing,
      handleUpdateBindValue,
      handleUpdateNode
    ]
  )

  return useMemo(() => ([state, callbacks]), [state, callbacks])
}
