import classNames from 'classnames'
import fuzzysort from 'fuzzysort'
import compact from 'lodash/compact'
import map from 'lodash/map'
import PropTypes from 'prop-types'
import { useCallback, useContext, useMemo, useState } from 'react'

import { treeToSequence, sequenceToTree } from './helpers'
import { SearchContext } from '../../providers'
import { bindNode } from '../../Keyboard/Binding/bindPropTypes'

import ResultList from '../../ValuePicker/ResultList'
import resultListStyle from '../../ValuePicker/style.module.css'
import SearchFilter from '../../ValuePicker/SearchFilter'
import ModifiersPicker from './ModifiersPicker'
import KeycodeBindNode from './KeycodeBindNode'
import { hydrateTree } from '../../Keyboard/Binding/util'
import KeyWrapper from '../../Keyboard/Keys/KeyWrapper'
import ActionButton from '../../Common/ActionButton'
import styles from './styles.module.css'
import Tip from '../../Common/Tip'
import Icon from '../../Common/Icon'

function renderSymbol (result) {
  const symbol = result?.symbol
  const icon = result?.faIcon

  if (icon) {
    return <Icon name={icon} />
  }

  if (symbol && !result.aliases.includes(symbol)) {
    return symbol
  }
}

function renderSearchResult (result) {
  const [titleResult] = result.search || []
  const title = titleResult
    ? <span dangerouslySetInnerHTML={{ __html: fuzzysort.highlight(titleResult) }} />
    : <span>{result.code}</span>

  const symbol = renderSymbol(result)

  return (
    <div className={styles.resultItem}>
      <div className={styles.description}>
        {title}
        <p className={resultListStyle.description}>{result.description}</p>
      </div>
      {symbol && <div className={styles.symbol}>{symbol}</div>}
    </div>
  )
}

function KeycodePicker ({ node, onSelect, behaviour }) {
  const context = useContext(SearchContext)
  const { getSearchTargets } = context

  const codes = useMemo(() => map(treeToSequence(node), 'source'), [node])
  const initialModifiers = codes.length === 1 ? [] : codes.slice(0, -1)
  const [selectedModifiers, setSelectedModifiers] = useState(map(initialModifiers, 'code'))
  const [selectedKeycode, setSelectedKeycode] = useState(codes.at(-1))

  const choices = useMemo(() => {
    // bit of a hack; we can't use the search picker to select the "function"
    // version of modifier keycodes because they won't end up in the modifiers
    // list and we aren't allowing for population of their parameters.
    // TODO: once this picker becomes the "standard" the function macros should
    // not be treated as keycodes.
    const codes = getSearchTargets('code', behaviour.code)
    return codes.filter(code => code.params.length === 0)
  }, [getSearchTargets, behaviour])

  // have to hydrate to get merged glyphs
  // hardcoding isn't great but it's the keycode picker so maybe '&kp' is reasonable
  const hydrated = hydrateTree({
    value: '&kp',
    params: [{
      value: selectedKeycode?.code,
      params: selectedKeycode?.params
    }]
  }, context)

  const select = useCallback(function (keycode, modifiers) {
    const tree = sequenceToTree(compact([...modifiers, keycode?.code]))

    onSelect(tree)
  }, [onSelect])

  const handleSelectKeycode = useCallback((result, event) => {
    if (event.type === 'dblclick') {
      select(result, selectedModifiers)
      return
    }

    setSelectedKeycode(result)
  }, [select, selectedModifiers, setSelectedKeycode])

  function handleConfirm () {
    select(selectedKeycode, selectedModifiers)
  }

  function handleCancel () {
    onSelect(node)
  }

  return (
    <div className={classNames(resultListStyle.dialog, styles.wrapper)}>
      <div style={{ display: 'flex', gap: '10px' }}>
        <div>
          <h3>Keycode</h3>
          <KeyWrapper className={styles.previewKeycode} selectable={false}>
            <KeycodeBindNode
              node={hydrated.params[0]}
              onSelect={() => {}}
            />
          </KeyWrapper>
        </div>
        <div>
          <h3>Modifiers</h3>
          <ModifiersPicker
            selected={selectedModifiers}
            implicit={selectedKeycode?.modifiers || []}
            onChange={setSelectedModifiers}
          />
        </div>
      </div>
      <h3>Available Keycodes</h3>
      <SearchFilter
        choices={choices}
        initialQuery={selectedKeycode?.code}
        searchKeys={['code', 'symbol']}
        renderResults={results => results && (
          <ResultList
            results={results}
            onSelect={handleSelectKeycode}
            renderResult={renderSearchResult}
          />
        )}
      />
      <Tip className={styles.quickCloseTip}>
        Double-click to select and close the dialog
      </Tip>
      <div style={{ marginTop: '10px', textAlign: 'center' }}>
        <ActionButton disabled={!selectedKeycode?.code} onClick={handleConfirm}>Confirm</ActionButton>
        <ActionButton onClick={handleCancel} secondary>Cancel</ActionButton>
      </div>
    </div>
  )
}

KeycodePicker.propTypes = {
  node: bindNode.isRequired,
  param: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.shape({
      enum: PropTypes.array,
      raw: PropTypes.bool,
      type: PropTypes.string
    })
  ]).isRequired,
  searchKey: PropTypes.string.isRequired,
  behaviour: PropTypes.object.isRequired,
  context: PropTypes.string.isRequired,
  onSelect: PropTypes.func.isRequired
}

export default KeycodePicker
